import { useNavigate } from "react-router-dom";

import { bgFileNumber, BGIMG } from "../../constants";
import { getUrlBase64 } from "../../utils";

import Header from "../header";
import useImgsLoaded from "../../hooks/useImgsLoaded";
import { Spin } from "antd";
import KeepAlive from "react-activation";

const bgArr: string[] = [];

new Array(bgFileNumber).fill(1).map(async (item, i) => {
  const img = await import(`../../assets/bgs/bg${i + 1}.jpeg`);
  bgArr.push(img.default);
  return img;
});

export default function Bgs() {
  const navigate = useNavigate();
  const loading = useImgsLoaded();

  const handleClick = async (item: string) => {
    const bgImg = await getUrlBase64(item);
    navigate(`/editor`, {
      state: { [BGIMG]: bgImg },
    });
  };

  return (
    <KeepAlive cacheKey="cache_bgs">
      <Header />
      <Spin spinning={loading}>
        <div className="bg-gray-100 flex justify-between flex-wrap">
          {bgArr.map((item, i) => (
            <div
              key={i}
              className="w-72 h-96 inline-block m-8 relative border-solid border-2 border-gray-300"
            >
              <div className="w-full h-full absolute bg-gray-500 opacity-0 hover:opacity-80">
                <div
                  className="m-auto mt-40 w-32  h-10 bg-yellow-500 text-center text-white leading-10 text-2xl font-medium rounded-md cursor-pointer"
                  onClick={() => handleClick(item)}
                >
                  
                </div>
              </div>
              <img className="w-full h-full" src={item} alt="" />
            </div>
          ))}
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
        </div>
      </Spin>
    </KeepAlive>
  );
}
